/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 115;
					$space_between_items: 14;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}

					.qodef-onsale,
					.qodef-new-product,
					.qodef-out-of-stock {
						left: 140px;
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 0;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 3px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 19px 0 0;
			font-weight: 700;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();
			font-family: $heading-text-font;
			font-size: 16px;
			font-weight: 700;

			del {
				margin: 0 6px 0 0;
			}

			ins {
				font-size: 20px;
				color: $first-main-color;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 0.7;
		    margin: 0 0 3px;

			.woocommerce-review-link {
				margin-left: 6px;
				font-size: 15px;
				vertical-align: middle;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 15px 0 0;
		
		    p {
				margin: 0 20% 0 0;
		    }
	    }

		.qodef-single-product-meta-title {
			display: block;
			margin-top: 29px;
			font-family: $heading-text-font;
			font-size: 16px;
			font-weight: 700;
			text-transform: uppercase;
		}
	
	    .product_meta {
		    margin: 5px 0 0;
		
		    > span {
			    display: block;
			    margin: 0;
				font-family: $heading-text-font;
				font-size: 14px;
				font-weight: 700;
				text-transform: uppercase;
			
			    a {
					font-family: $default-text-font;
					font-size: 15px;
					font-weight: 400;
				    color: $default-text-color;
					text-transform: none;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }

				span {
					font-family: $default-text-font;
					font-size: 15px;
					font-weight: 400;
					text-transform: none;
				}
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 27px 0 0;
			font-family: $heading-text-font;
			font-size: 16px;
			font-weight: 700;
			text-transform: uppercase;

			.qodef-social-title {
				margin: 0 11px 0 0;
			}
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }

			ul {
				vertical-align: text-bottom;

				li {
					margin-right: 15px;

					a {
						font-size: 13px;
					}
				}
			}
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
			font-family: $heading-text-font;
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 33px 0 3px;

			.single_add_to_cart_button {
				float: left;
			}
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 0 0 6px;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 61px 0 0;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				float: left;
				margin-bottom: -1px;
				padding: 0;
				vertical-align: middle;
				transition: border-bottom-color .4s ease-out;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 26px 16px;
					font-family: $heading-text-font;
					font-size: 16px;
					font-weight: 700;
					text-transform: uppercase;
					
					&:after {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						display: block;
						width: 0;
						height: 3px;
						line-height: 1;
						background-color: $first-main-color;
						-webkit-transition: width .3s ease;
						-moz-transition: width .3s ease;
						transition: width .3s ease;
					}
					
					&:hover {
						color: inherit;
						
						&:after {
							width: 100%;
						}
					}
				}

				&:first-child {
					padding-left: 0;

					a {
						padding-left: 0;
					}
				}

				&.active {
					a:after {
						width: 100%;
					}
				}
				
				@include phone-portrait {
					display: block;
					width: 100%;
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 17px 17% 20px 0;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-family: $heading-text-font;
				font-size: 14px;
				font-weight: 700;
				text-transform: uppercase;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				@include qodefHeadingStyle(h5);
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 80px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 103px;

					.star-rating {
						margin: 0 0 13px;
					}
					
					p {
						margin: 0 0 6px;

						.woocommerce-review__author {
							@include qodefHeadingStyle(h6);
						}

						.woocommerce-review__published-date {
							font-size: 12px;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
					@include qodefHeadingStyle(h5);
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}

	&.qodef-woo-pl-info-below-image {

		.related.products,
		.upsells.products {
			margin: 30px 0 0;

			> h2 {
				display: block;
				margin: 42px 0 38px;
				text-align: left;
				font-size: 26px;
				line-height: 1.385em;
			}

			.product {
				margin: 0 0 10px;

				.qodef-pl-text-wrapper {
					margin-top: 21px;
					margin-bottom: 0;

					.qodef-product-list-title {
						margin: 0 0 14px;
					}
				}
			}
		}
	}

	&.qodef-woo-pl-info-on-image-hover {

		.related.products,
		.upsells.products {
			margin: 30px 0 0;

			> h2 {
				display: block;
				margin: 42px 0 38px;
				text-align: left;
				font-size: 26px;
				line-height: 1.385em;
			}

			.product {

				.qodef-pl-text {
					background-color: rgba(0, 0, 0, 0.46);
				}

				.qodef-product-list-title {
					float: none;
					display: block;
					margin: 0 0 3px;
				}

				.qodef-pl-category {
					float: none;
					background-color: transparent;
					color: $default-background-color;

					a {
						color: $default-background-color;
					}

					&:before {
						display: none;
					}
				}

				.price {
					float: none;

					del {
						color: $default-background-color;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */