/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid #5e5e5e;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 50px;
            width: 50px;
            padding: 0;
            margin: 0 25px 0 0;
            font-family: $heading-text-font;
            line-height: 42px;
            outline: 0;
            
            @include phone-landscape {
                width:40px;
            }
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            right: 0;
            width: 25px;
            height: 25px;
            line-height: 25px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .qodef-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */