/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonSolidColor();
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include qodefButtonSolidHoverColor();
        }
    }
    
    a.button, button[type="submit"]:not(.qodef-search-submit) {

        &:after {
            content: "";
            @include qodefAbsoluteHolderLayout();
            background-color: $lighter-main-hover-color;
            transform: scaleX(0);
            transform-origin: 100% 0;
            transition: transform .4s cubic-bezier(0.72, 0.07, 0.22, 0.89);
            z-index: -1;
        }

        &:hover {
            background-color: $first-main-color !important;

            &:after {
                transform: scaleX(1);
                transform-origin: 0 0;
            }
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */