/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    margin: 0;
    padding: 0 16px;
    color: #fff;
    font-size: 8px;
    line-height: 24px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
    top: 14px;
    left: 11px;
    width: 78px;
    height: 78px;
    border-radius: 50%;
    background-color: $first-main-color;

    span {
        position: absolute;
        display: inline-block;
        top: calc(50% - 4px);
        left: 5px;
        font-family: $additional-text-font;
        font-size: 50px;
        line-height: 1em;
        color: #000;
        text-transform: lowercase;
        transform: skewY(-10deg) translateY(-50%);
    }
}

@mixin qodefWooCommerceSaleStyle() {
    background-color: #000;

    span {
        left: 1px;
        color: $default-background-color;
    }
}

@mixin qodefWooCommerceOutOfStockStyle() {
    background-color: #4e4e4e;

    span {
        left: 1px;
        color: $default-background-color;
    }
}

@mixin qodefWooCommerceNewProductStyle() {

}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 8px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
        color: $first-main-color;
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
            color: $first-main-color;
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: 2;
        color: $first-main-color;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: -4px;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
	    letter-spacing: 12.1px;
        color: $first-main-color;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */