/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .qodef-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {

        .qodef-widget-title {
            margin: 0 0 33px;
        }
        
        .price_slider_wrapper {
            width: 98.5%;
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #000;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 9px;
                    width: 9px;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        transform: translateX(-50%);
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 33px 0 0;
            
            .button {
                float: right;
                padding: 0;
                background-color: transparent;
                border: none;
                color: $default-heading-color;
                line-height: 1em;
                transition: color .2s ease-out;

                &:hover {
                    color: $first-main-color;
                    background-color: inherit;
                }
            }
            
            .price_label {
                float: left;
                font-family: $heading-text-font;
                font-size: 14px;
                font-weight: 700;
                font-style: normal;
                line-height: 1em;
                text-transform: uppercase;
            }
        }
    }
    
    &.widget_product_categories {

        .qodef-widget-title {
            margin: 0 0 20px;
        }
        
        ul {
            
            li {
                margin: 3px 0;
                font-family: $heading-text-font;
                font-size: 16px;
                font-weight: 700;
                text-transform: uppercase;
                
                ul {
                    margin: 0 0 0 10px;
                }

                a {

                    &:hover {

                        + .count {
                            color: $first-main-color;
                        }
                    }
                }

                .count {
                    transition: color .2s ease-out;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }

    &.widget_products {

        .qodef-widget-title {
            margin: 0 0 29px;
        }

        ul {

            li {
                margin: 0 0 29px;
                min-height: 65px;
                @include qodefImageZoomHoverStyle();

                a {

                    img {
                        max-width: 88px;
                        max-height: 65px;
                        object-fit: cover;
                    }

                    .product-title {
                        padding-top: 4px;
                        font-family: $heading-text-font;
                        font-size: 16px;
                        font-weight: 700;
                        line-height: 1em;
                        text-transform: uppercase;
                    }
                }

                .star-rating {
                    margin-top: 0;
                    margin-bottom: 5px;

                    span {

                        &:before {
                            color: $first-main-color;
                        }
                    }

                    &:before {
                        color: $first-main-color;
                    }
                }

                .woocommerce-Price-amount {
                    font-family: $heading-text-font;
                    font-size: 14px;
                    font-weight: 700;
                    text-transform: uppercase;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {

        .qodef-widget-title {
            margin: 0 0 28px;
        }
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 60px;
                line-height: 32px;
                color: $default-text-color;
                background-color: #fafafa;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                transition: color .2s ease-out;
            }
            
            input {
                width: calc(100% - 45px);
                padding: 10px 10px 10px 25px;
                font-family: inherit;
                font-size: 13px;
    
                @include qodefPlaceholder {
                    font-size: 12px;
                    color: inherit;
                }
            }
            
            button {
                width: 45px;
                padding: 10px 25px 10px 0px;
                font-size: 19px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    &.qodef-social-icons-group-widget {

        .qodef-widget-title {
            margin: 0 0 23px;
        }
    }

    &.qodef-social-icons-group-widget.qodef-footer-column-1 {

        .qodef-social-icon-widget-holder {
            margin: 0 21px 0 0;
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */