/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .qodef-sc-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            transition: .5s cubic-bezier(0.66, 0.14, 0.58, 1) .1s;
        }
    }

    .qodef-shopping-cart-inner {
        @include qodefRelativeHolderLayout();
        height: 100%;
    }

    &.qodef-shopping-cart-border .qodef-header-cart {
        border-left: 1px solid #ebebeb;
    }

    .qodef-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 37px;
        justify-content: center;
        
        &:hover {
            color:inherit;
        }

        .qodef-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;

            .qodef-sc-opener-count {
                position: absolute;
                top: -5px;
                right: -6px;
                font-size: 11px;
                background-color: $first-main-color;
                color: $default-heading-color;
                border-radius: 50%;
                width: 14px;
                height: 14px;
                text-align: center;
                line-height: 13px !important;
            }
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-sc-opener-icon {
                font-size: 22px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-sc-dropdown {
    position: absolute;
    top: 100%;
    right: auto;
    left: 0;
    text-align: left;
    box-sizing: border-box;
    color: #fff;
    max-height: 0;
    pointer-events: none;
    overflow: hidden;
    will-change: transform;
    transition: .2s;

    .qodef-sc-dropdown-mirror & {
        right: auto;
        left: 0;
    }

    h1, h2, h3, h4, h5, h6 {
        color: #fff;
    }
	
	.qodef-empty-cart {
		display: block;
		padding: 7px 15px;
        font-size: 14px;
	}

    .qodef-sc-dropdown-inner {
        @include qodefRelativeHolderLayout();
        width: 309px;
        padding: 14px;
        background-color: #111111;
        box-sizing: border-box;
    }
    
    .qodef-sc-dropdown-items {
        @include qodefRelativeHolderLayout();
    }
    
    .qodef-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 14px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .qodef-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 74px;
        margin-right: 12px;
        
        a, img {
            display: block;
        }
    }
    
    .qodef-sc-dropdown-item-content {
        @include qodefRelativeHolderLayout();
        margin-top: 11px;
        padding-right: 20px;
    }
    
    .qodef-sc-dropdown-item-title {
        margin: 0;
    }

    
    .qodef-sc-dropdown-item-price,
    .qodef-sc-dropdown-item-quantity {
        font-size: 15px;
        font-family: $heading-text-font;
        font-weight: 700;
    }

    .qodef-sc-dropdown-item-remove {
        position: absolute;
        top: -2px;
        right: 0;
        font-size: 20px;
        line-height: 1;
        color: #fff;

        &:hover {
            color: $lighter-main-hover-color;
        }
    }
    
    .qodef-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 8px;
        
        > * {
            margin: 0;
            font-size: 15px;
            font-weight: 700;
            font-family: $heading-text-font;

            &.qodef-sc-dropdown-total{
                font-size: 14px;
            }

        }
    }
    
    .qodef-sc-dropdown-button-holder {
        @include qodefRelativeHolderLayout();
        margin-top: 10px;
    }
    
    .qodef-sc-dropdown-button {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize(huge);
        @include qodefButtonSolidColor();
        width: 50%;
        float: left;
        padding-left: 5px;
        padding-right: 5px;
        text-align: center;
        font-size: 14px;

        &.qodef-sc-dropdown-button-cart {
            background-color: $first-main-color;
            &:hover {
                background-color: $lighter-main-hover-color;
            }
        }

        &.qodef-sc-dropdown-button-checkout {
            background-color: #1f1f1f;
            color: #fff;
        }

        &:hover {
            background-color: #191919;
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */