/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
        font-family: $default-text-font;
    }

    .woocommerce-result-count {
        font-size: 14px;
        line-height: #{$sselect-input-height + 3}px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */