/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();
}

.qodef-pl-main-holder {
    margin: 0 0 22px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;

        .product {

            .qodef-pl-text {
                background-color: rgba(0, 0, 0, 0.46);
            }

            .qodef-product-list-title {
                float: none;
                display: block;
                margin: 0 0 3px;
            }

            .qodef-pl-category {
                float: none;
                background-color: transparent;
                color: $default-background-color;

                a {
                    color: $default-background-color;
                }

                &:before {
                    display: none;
                }
            }

            .price {
                float: none;

                del {
                    color: $default-background-color;
                }
            }
        }
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        @include qodefImageZoomHoverStyle();
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .qodef-pl-inner .qodef-pl-text {
                opacity: 1;
            }
        }
        
        .qodef-product-list-title {
            display: inline-block;
            float: left;
            margin: 0 0 12px;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
            display: none;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
            margin: 5px 0 0 0;
            display: inline-block;
            float: right;
            font-family: $heading-text-font;
            font-size: 16px;
            font-weight: 700;

            del {
                margin: 0 6px 0 0;
            }

            ins {
                color: $first-main-color;
            }
        }

        .qodef-pl-category {
            position: relative;
            display: block;
            float: left;
            padding: 0 11px;
            background-color: $first-main-color;
            z-index: 0;
            clear: left;
            font-family: $heading-text-font;
            font-size: 13px;
            font-weight: 700;
            text-transform: uppercase;

            a {

                &:hover {
                    color: inherit;
                }
            }

            &:before {
                content: '';
                position: absolute;
                display: block;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                z-index: -1;
                background-color: $first-main-color;
                transform-origin: bottom left;
                -ms-transform: skew(-15deg, 0deg);
                -webkit-transform: skew(-15deg, 0deg);
                transform: skew(-15deg, 0deg);
            }
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .qodef-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                opacity: 0;
                box-sizing: border-box;
                transition: opacity .2s ease-out;
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();

                > a {
                    position: absolute;
                    left: 0;
                    bottom: -1px;
                    padding: 9px 41px 6px 30px;
                    background-color: $default-background-color;

                    &:after {
                        content: none;
                    }

                    &:before {
                        content: '';
                        display: inline-block;
                        margin-right: 5px;
                        margin-bottom: 3px;
                        width: 41px;
                        height: 2px;
                        background-color: #111;
                        vertical-align: middle;
                    }

                    &:hover {
                        background-color: $first-main-color;
                    }
                }
                
                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            margin: 14px 0 40px;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

body {

    .select2-container--default {

        .select2-selection--single {
            font-family: $default-text-font;
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */