/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	
	&.qodef-info-on-image {
		
		.qodef-pli {

			.qodef-pli-text {
				background-color: rgba(0, 0, 0, 0.46);
			}

			.qodef-pli-title {
				float: none;
				display: block;
			}

			.qodef-pli-category {
				float: none;
				background-color: transparent;

				&:before {
					display: none;
				}
			}

			.qodef-pli-excerpt {
				float: none;
				text-align: center;
			}

			.qodef-pli-price {
				float: none;

				del {
					color: $default-background-color;
				}
			}
			
			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .qodef-pli {
		margin-bottom: 64px;
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
			display: inline-block;
			float: left;
			margin: 0 0 10px;
	    }
	
	    .qodef-pli-category {
			position: relative;
			display: block;
			float: left;
			margin: 0;
			padding: 0 11px;
			background-color: $first-main-color;
			z-index: 0;
			clear: left;
			font-family: $heading-text-font;
			font-size: 13px;
			font-weight: 700;
			text-transform: uppercase;

			a {

				&:hover {
					color: inherit;
				}
			}

			&:before {
				content: '';
				position: absolute;
				display: block;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				z-index: -1;
				background-color: $first-main-color;
				transform-origin: bottom left;
				-ms-transform: skew(-15deg, 0deg);
				-webkit-transform: skew(-15deg, 0deg);
				transform: skew(-15deg, 0deg);
			}
	    }

        .qodef-pli-excerpt {
			float: left;
			clear: left;
			margin: 17px 0 3px;
            color: $default-heading-color;
	        line-height: 1.3em;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
			margin: 4px 0 0;
			display: inline-block;
			float: right;
			font-family: $heading-text-font;
			font-size: 16px;
			font-weight: 700;

			del {
				margin: 0 6px 0 0;
			}

			ins {
				color: $first-main-color;
			}
        }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
		@include qodefImageZoomHoverStyle();

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

			.qodef-pli-onsale,
			.qodef-pli-out-of-stock,
			.qodef-pli-new-product {
				@include qodefWooCommerceMarksGeneralStyle();
			}

			.qodef-pli-onsale {
				@include qodefWooCommerceSaleStyle();
			}

			.qodef-pli-out-of-stock {
				@include qodefWooCommerceOutOfStockStyle();
			}

			.qodef-pli-new-product {
				@include qodefWooCommerceNewProductStyle();
			}
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            opacity: 0;
            box-sizing: border-box;
            transition: opacity .2s ease-out;
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }
	
	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .qodef-pli-add-to-cart {
				position: absolute;
				left: 0;
				bottom: -1px;

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();
						padding: 9px 41px 6px 30px;
						background-color: $default-background-color;

						&:before {
							content: '';
							display: inline-block;
							margin-right: 5px;
							margin-bottom: 3px;
							width: 41px;
							height: 2px;
							background-color: #111;
							vertical-align: middle;
						}

				        &:hover {
				            @include qodefButtonSolidHoverColor();
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-background-color;
				        background-color: #000;

						&:before {
							content: '';
							display: inline-block;
							margin-right: 5px;
							margin-bottom: 3px;
							width: 41px;
							height: 2px;
							background-color: $default-background-color;
							vertical-align: middle;
							transition: background-color .2s ease-out;
						}

				        &:hover {
					        color: #000;
					        background-color: $first-main-color;

							&:before {
								background-color: #000;
							}
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 14px 0 0;
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */